a.icg-btn {
  color: inherit;
  text-decoration: inherit;
  font-size: 2em;
  margin: 2em 0;
  border: 2px solid #fff;
  padding: 0.75em;
  border-radius: 0.2em;
  box-shadow: 0px 2px 2px black;
  cursor: pointer;
  transition: all 0.3s ease 0s; }
  a.icg-btn:hover {
    background: rgba(250, 250, 250, 0.25); }
  a.icg-btn.borderless {
    border: none;
    box-shadow: none; }
    a.icg-btn.borderless:hover {
      background: transparent; }
