.icg-footer {
  background-color: #333;
  height: 300px;
  overflow: hidden;
  position: fixed;
  text-align: center;
  width: 100%;
  z-index: -99;
  display: grid;
  bottom: 0;
  left: 0;
  color: #fff; }
  .icg-footer .footer-content {
    text-align: left;
    align-items: baseline;
    width: 100%;
    height: 100%;
    margin: 100px 0 0 0; }
    .icg-footer .footer-content .footer-row {
      display: inline-block;
      width: inherit; }
      .icg-footer .footer-content .footer-row .footer-links, .icg-footer .footer-content .footer-row .footer-logo {
        display: inline-flex;
        vertical-align: top;
        position: relative; }
      .icg-footer .footer-content .footer-row .footer-links {
        float: right;
        top: 22px;
        right: 20px; }
        .icg-footer .footer-content .footer-row .footer-links a {
          color: #999; }
          .icg-footer .footer-content .footer-row .footer-links a:hover {
            color: #fff; }
          .icg-footer .footer-content .footer-row .footer-links a svg {
            cursor: pointer;
            fill: currentColor;
            vertical-align: bottom; }
      .icg-footer .footer-content .footer-row .footer-logo {
        float: left;
        left: 20px; }
      .icg-footer .footer-content .footer-row ul {
        display: inline-flex;
        margin: 0;
        padding: 0; }
        .icg-footer .footer-content .footer-row ul li {
          list-style: none;
          margin: 0 0.45em; }
          .icg-footer .footer-content .footer-row ul li a {
            font-style: normal;
            font-weight: 600; }
    .icg-footer .footer-content .copyright {
      font-size: 0.95em;
      font-weight: lighter; }
    .icg-footer .footer-content .madeWithLove {
      line-height: 60px; }
      .icg-footer .footer-content .madeWithLove i {
        line-height: 21px;
        color: pink;
        vertical-align: middle; }
